.number-keyboard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

.number-keyboard .keyboard-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

.pin-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 6.4rem;
}
.pin-input {
  width: 8rem;
  height: 8rem;
  border: none;
  border-bottom: 1px solid var(--gold);
  outline: none;
  text-align: center;
  background: transparent;
  font-size: 4rem;
  font-weight: bold;
  color: var(--gold);
}
