.tables {
  height: calc(100% - 15rem);
}

.tables .tables__zone {
  height: calc(100% - 12rem);
  background-color: var(--gray);
}

.tables .tables-bottom__bar {
  height: 12rem;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.6rem;
  box-sizing: border-box;
}

.tables .tables-bottom__bar .tabels-zone__list {
  display: flex;
  height: 100%;
}

.tables .tables-bottom__bar .tabels-zone__list .tabels-zone__list-item {
  width: 20rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.tables .tables-bottom__bar .tabels-zone__list .tabels-zone__list-item span {
  font-size: 3rem;
  font-weight: bold;
  color: #000;
}

.tables .tables-bottom__bar .tabels-zone__list .tabels-zone__list-item::after {
  content: "";
  position: absolute;
  top: -2rem;
  left: calc(50% - 2.75rem);
  /* transform: translate(-50%, -50%); */
}

.tables
  .tables-bottom__bar
  .tabels-zone__list
  .tabels-zone__list-item.active::after {
  border: 1.5rem solid;
  border-radius: 50%;
  border-color: var(--gray);
  width: 2rem;
  height: 2rem;
  background-color: var(--gray-gold);
}

.tables .tables-bottom__bar .tables-button__takeaway {
  width: 20rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.tables .tables-bottom__bar .tables-button__takeaway span {
  font-size: 2.4rem;
  font-weight: bold;
  color: #000;
  text-transform: uppercase;
}

.tables .tables-bottom__bar .tables-button__takeaway img {
  width: 6rem;
  height: 6rem;
}
