.table {
  margin: 0 0 40px 0;
  width: 100%;
  display: table;
}

.row {
  display: table-row;
  background: #fff;
  font-size: 2rem;
  height: 1.8rem;
}
.row:nth-of-type(even) {
  background: var(--gray);
}
.row.header {
  font-weight: 900;
  color: #000;
  /* background: #ea6153; */
}
.cell {
  padding: 1.2rem 1.2rem;
  display: table-cell;
  color: #000;
  font-weight: 400;
}
