@import-normalize;

:root {
  --blue: rgb(43, 55, 78);
  --blue-opacity: rgba(43, 55, 78, 0.9);
  --gray: rgb(183, 185, 187);
  --gray-gold: rgb(197, 187, 162);
  --gold: rgb(234, 221, 190);
  --gray: rgb(230, 231, 232);
  --green: rgb(149, 196, 126);
  --border-bottom-radius: 2.4rem;

  font-size: 10px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;

  max-height: 100vh;
  height: 100%;
}

