.menu {
  height: calc(100% - 15rem);
  position: relative;
}

.menu .menu-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 1.6rem;
  padding: 2.6rem 1.6rem;
}

.menu .menu-list .menu-list__item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  max-width: 20rem;
  width: 100%;
  height: 10rem;
  padding: 0 1rem;
  border-radius: 2rem;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  cursor: pointer;
}

.menu .menu-list .menu-list__item img {
  width: 6rem;
  height: 6rem;
}

.menu .menu-list .menu-list__item .menu-list__item-label {
  font-size: 2.4rem;
  font-weight: 300;
  color: var(--blue);
  text-align: center;
}

.menu .menu-selected {
  background-color: var(--gray);
  height: calc(100% - 36.8rem);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 1.6rem 3.2rem;
}

.menu .menu-selected .menu-selected__id {
  font-weight: bold;
  font-size: 2.4rem;
  text-transform: uppercase;
  margin: 2rem 0;
}

.menu .menu-bottom__bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 10rem;
  background-color: var(--blue);
}
