.login-page {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: stretch;
  align-items: stretch;
  gap: 0;

  position: relative;
  width: 100%;
  height: 100%;
}

.login-page .login-page__block {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 50%;
  height: 100%;
}

.login-page .login-page__block.login-page__left {
  background-position: center;
  background-origin: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.login-page .login-page__block.login-page__right {
  position: relative;
  background-color: var(--blue-opacity);
}

.login-page .login-page__block.login-page__right::before {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  content: "";
  background-position: right;
  /* background-origin: center right; */
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../../res/images/chicago.jpg);
}
