.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  background-color: var(--blue-opacity);
  height: 15rem;
  padding: 0 1.6rem;
}

.header::before {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  content: "";
  background-position: center;
  /* background-origin: center right; */
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../res/images/chicago.jpg);
}

.header .time {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.6rem;
}

.header .time .time_digits {
  font-size: 6rem;
  color: var(--gold);
  font-weight: bold;
}

.header .time .time_location {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.header .time .time_location .date {
  font-size: 2rem;
  color: var(--gold);
  font-weight: bold;
}

.header .time .time_location .location {
  font-size: 2rem;
  color: #fff;
}

.header .staff {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.6rem;
}

.header .staff .staff-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.header .staff .staff-info .staff-info__name {
  font-size: 2rem;
  color: var(--gold);
  font-weight: bold;
}

.header .staff .staff-info .staff-info__position {
  font-size: 2rem;
  color: #fff;
}

.header .staff .staff-exit,
.header .staff .staff-user {
  width: 8rem;
  height: 8rem;
}
